#menu-links {
  display: flex;
  padding: 2em;
  justify-content: flex-end;
}

#menu-links a { 
  padding-right: 0.5em;
  color: #7D4CDB;
  text-decoration: none;
  cursor: pointer;
}

.logo {
  height: 70px;
  width: 70px;
  border-radius: 25%;
  padding: .5em;
}

.siteName {
  padding: .5em;
}

.menuBar {
  display: flex;
  box-sizing: border-box;
  outline: none;
  margin: .5em;
  padding: .5em;
  border-radius: 12px;
}

